import axios from 'axios'
import qs from 'qs'
import {baseUrl} from './config'
import {Toast} from 'mint-ui'

// 封装数据返回成功提示函数
function successState(res){
    if(res.data.code==='000000'){
        alert('success')
        return res
    }
}
// 封装数据返回失败提示函数
function errorState(response){
    if(response && (response.status === 200 || response.status === 304 || response.status === 400)){
        // 如果不需要除了data之外的数据，可以直接 return response.data
    return response
    }else{
        Toast({
            message:'网络异常',
            position:'bottom',
            duration: 5000 //持续时间（毫秒），若为-1则不会自动关闭
        })
    }
}
const httpserver=(method,url,params)=>{
    
    let httpDefault={
        method:method,
        baseURL: baseUrl,
        url:url,
        // `params` 是即将与请求一起发送的 URL 参数
        // `data` 是作为请求主体被发送的数据
        params:method==='GET' || method==='DELETE' ? params:null,
        data:method==='POST' || method==='PUT' ? qs.stringify(params):null,
        timeout:10000,
    }
    return new Promise((resolve,reject)=>{
        axios(httpDefault)
        // 此处的.then属于axios
        .then((res)=>{
            console.log(res)
            resolve(res.data)
        }).catch((response)=>{
            errorState()
            reject(response)
        })
    })
}
// 输出函数getAxios、postAxios、putAxios、delectAxios，供其他文件调用-----------------------------
// Vue.js的插件应当有一个公开方法 install。这个方法的第一个参数是 Vue 构造器，第二个参数是一个可选的选项对象。
/* export default{
    install:function(Vue){
        Vue.prototype.getAxios=(url,params)=>httpserver('GET',url,params)
        Vue.prototype.postAxios=(url,params)=>httpserver('POST',url,params)
        Vue.prototype.putAxios=(url,params)=>httpserver('PUT',url,params)
        Vue.prototype.deleteAxios=(url,params)=>httpserver('DELETE',url,params)
    }
} */
export default httpserver


