<template>
    <div>
        <ul>
            <li id="name">{{name}}</li>
            <li>{{age}}</li>
            <li>{{city}}</li>
        </ul>
        <button type="button" class="btn btn-primary" @click="submit">primary</button>
    </div>
    
</template>

<script>
import {getFindByName} from './../../service/test/zhouqiGetData'
    export default{
        name:"ZhouQi",
        data(){
            return{
                name:"",
                age:"",
                city:""
            }
        },
        created(){
            console.log(__dirname)
            console.log(111)
            this.name="梁亮亮"
            this.age="30"
            this.city="北京"
            var x = document.getElementById("name")
        },
        methods:{
            submit(){
                 getFindByName('曹操').then((data)=>{
                    console.log(data.data)
                }).catch((err)=>{
                    console.log(err)
                })
            }
        },
        mounted(){
            var x = document.getElementById("name")
        }
    }
</script>


